import ViewportInterface from './ViewportInterface';

export default class AlignmentServiceViewport extends ViewportInterface {

    constructor(...args) {
        super(...args);

        this.fetchSheetNodeForViewport = this.options.fetchSheetNodeForViewport;
        this.fetch3DNodeForViewport = this.options.fetch3DNodeForViewport;
        this.getModel3DInfoRelatedToViewport = this.options.getModel3DInfoRelatedToViewport;
    }

    getGuid() {
        return this.viewportRaw.regionId;
    }

    isMatrixInLocalCoords() {
        return false;
    }

    getViewportBounds() {
        return this.viewportRaw.boundingBox;
    }

    get2DTo3DMatrix() {
        return this.viewportRaw.transform;
    }

    async getParentSheetNode() {
        if (!this.parentSheetNodePromise) {
            this.parentSheetNodePromise = this.fetchSheetNodeForViewport(this);
        }
        
        return this.parentSheetNodePromise;
    }

    async get3DNodesForViewport() {
        return this.fetch3DNodeForViewport(this);
    }

    // Returns { versionUrn3D, lineageUrn3D, name3D } for the 3D model related to a viewport
    // Will throw if no model found or has no permissions
    async getRelatedModel3DInfo() {
        return this.getModel3DInfoRelatedToViewport(this);
    }

    findLevelForViewport() {
        if (!this.isTopViewViewport()) {
            return null;
        }

        // Generate synthetic level data.
        return { elevation: this.getElevation() };
    }

    isViewportInLevel(level) {
        if (!this.isTopViewViewport()) {
            return false;
        }

        const z = this.getElevation();

        // Notice that both matrix and level are in world coordinates.
        return z > level.zMin && z < level.zMax;
    }

    supports2DTo3DTransform() {
        return !!this.options.supports2DTo3DTransform;
    }

    isTopViewViewport() {
        if (this._topViewViewport === undefined) {
            const matrix = this.get2DTo3DMatrix();
            const normalMatrix = new THREE.Matrix3().getNormalMatrix(matrix);
            const z = new THREE.Vector3(0, 0, 1);

            const direction = z.clone().applyMatrix3(normalMatrix);
            let angle = direction.angleTo(z);

            // Support upside down viewports
            if (angle >= Math.PI) {
                angle -= Math.PI;
            }

            const AngleThreshold = 0.05;
            this._topViewViewport = Math.abs(angle) < AngleThreshold;
        }

        return this._topViewViewport;
    }

    isFloorPlan() {
        // Assume all top-view manually aligned viewports are floor plans.
        return this.isTopViewViewport();
    }
}
