// A single Level.
// Usually levels are being extracted from AecModelData - but can be generated from other sources (artificially created from viewports).
export default class Level {
    constructor(index, guid, name, zMin, zMax) {
        this.index = index;
        this.name = name;
        this.zMin = zMin;
        this.zMax = zMax;
        this.guid = guid;
    }
}
